@import url('https://fonts.googleapis.com/css2?family=Fresca&family=Varela+Round&display=swap');

#dashboard-content{
    display: flex;
    height: 100%;
    font-family: 'Fresca', sans-serif;
    font-family: 'Varela Round', sans-serif;
    /* font-size: 1.25em; */
}

.dashboard-nav-off{
    position: fixed;
    display: flex;
    width: 100%;
    align-items: center;
    height: 7%;
    background-color: #000;
    color: orange;
}

.dashboard-nav-li{
    display: flex; 
    list-style-type: none;
    width: 100%; 
    margin: 0; 
    justify-content: space-evenly;
}

.dashboard-nav-li a, .dashboard-nav-li a:visited{
    text-decoration: none;
    color: blue;
}


@media screen and (max-width: 450px) {
    #header{
        position: fixed;
        width: 100%;
        height: 4%;
        background-color: #000;
        z-index: 1;
    }

    .dashboard-nav-off{
        display: flex;
        align-items: flex-start;
        padding: 2%;
        background-color: #000;
        color: orange;
        height: 100%;
        width: 35%;
        position: fixed;
        transform: translateX(-100%);
        transition: .4s;
    }
    .dashboard-nav-on{
        display: flex;
        align-items: flex-start;
        padding: 2%;
        background-color: #000;
        color: orange;
        height: 100%;
        width: 35%;
        position: fixed;
        transform: translateX(0%);
        transition: .4s;
    }
    
    .dashboard-nav-li{
        display: flex;
        list-style-type: none;
        width: 100%;
        height: 40%;
        flex-direction: column;
    }
}
/*Styles from here are focused on the houses container*/
#dashboard-container{
    width: 100%;
}

.dashborad-main-heading{
    text-align: center;
    margin: 5% 0;
}

@media screen and (max-width: 850px) {
    .dashborad-main-heading{
        text-align: center;
        margin: 8.5% 0;
    }
}

.add-houses-btn{
    display: block;
    margin: 0 auto;
    background: #000;
    color: orange;
    padding: 1%;
    border-radius: 20px;
}

/*Styles from here are focused on the house modal*/
#modal-container{
    display: flex;
    position: fixed;
    height: 100vh;
    background: #00000087;
    width: 100%;
    z-index: 2;
}

#house-modal{
    height: 100%;
}

.house-form{
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    width: 50%;
    min-width: 220px;
    max-width: 530px;
    background-color: #fff;
    border-radius: 20px;
    text-align: center;
    font-weight: bold;
}

label{
    color: #000;
    margin: 1% auto;
}

.house-form input{
    display: block;
    margin: 0 auto;
    text-align: center;
    border: 2px dotted;
    border-radius: 15px;
    max-width: 220px;
    /* min-width: 130px; */
}

.house-form input[type='file']{
    width: 22%;
    max-width: 130px;
    min-width: 130px;
    margin: 1% auto;
    font-weight: bold;
}

.submit-btn{
    padding: 0.5%;
    font-weight: bold;
    background: orange;
    border-radius: 10px
}

/*Styles from here are focues on the house-container*/
table{
    table-layout: fixed;
    margin: 0 auto;
    font-weight: 500;
}

tr{
    text-align: center;
}

#horizontal-line{
    height: 3px;
}

td{
    background-color: orange;
}

