@import url('https://fonts.googleapis.com/css2?family=Fresca&family=Varela+Round&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Fresca', sans-serif;
    font-family: 'Varela Round', sans-serif;
}

html, body, #root, .App{
    height: 100%;
}

#login-content{
    display: flex;
    height: 100vh;
}

.login-image-container{
    height: 100%;
    width: 100%;
}

.login-img{
    display: block;
    width: 100%;
    height: 100%;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
}

.form{
    margin: 0 auto;
    width: 70%;
}

h2{
    margin: 2% 0;
    text-align: center;
}

hr{
    background-color: #ef7900;
    border: 2px solid #ef7900;
    width: 60%;
    margin: 0 auto;
}

.form-container{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
}

.login-input{
    display: block;
    margin: 20% auto;
    padding: 2%;
    width: 100%;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 2px solid #ef7900;
    font-weight: 700;
}

.login-input::placeholder{
    color: black;
}

.login-input[type='submit']{
    background: #ef7900;
    border: none;
    border-radius: 30px;
}

.login-input:focus{
    outline: none;
}


@media screen and (min-width: 280px) and (max-width: 1028px){
    .login-image-container{
        min-width: 280px;
        max-width: 690px;
    }

    .form-container{
        min-width: 280px;
        max-width: 660px;
    }
    
}

@media screen and (max-width: 700px){
    #login-content{
        display: flex;
        flex-direction: column;
    }
}

@media screen and (max-width: 700px){
    
    .login-img{
        border-top-right-radius: 0px;
        border-bottom-left-radius: 30px;
    }
}


