@import url('https://fonts.googleapis.com/css2?family=Fresca&family=Varela+Round&display=swap');
section{
    margin-bottom: 2%;
    font-family: 'Fresca', sans-serif;
    font-family: 'Varela Round', sans-serif;
}

/*Styles from here are focused on the 'Homepage id' section*/
#home-img-container{
    position: relative;
    height: 82%;
    z-index: -1;
}

.home7Img{
    display: block;
    width: 100%;
    height: 100%;
}

.view-home-text{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #fff;
    font-size: 1.1em;
}

.view-homes-btn{
    padding: 3%;
    border: 2px solid #fff;
    border-radius: 10px;
    color: orange;
    background-color: rgba(41, 40, 40, 0.822);
    font-weight: bold;
    text-decoration: underline;
    transition: .4s;
}

/*Styles from here are focused on the 'About id' section*/
#about{
    width: 85%;
    margin: 0 auto;
    border-radius: 20px;
    -webkit-box-shadow: 10px 5px 15px 5px #000000; 
    box-shadow: 10px 5px 15px 5px #363636;
}
#about > .about-contents, .about-img-container{
    margin-bottom: 1.5%;
}

.about-contents > p, .about-img-container{
    padding: 0 2%;
}

h3{
    text-align: center;
}


.about-contents{
    display: flex;
}

.about-img-container{
    width: 100%;
}

.boughtHouseImg{
    display: block;
    width: 100%;
    height: 100%;
}

/*Styles from here are focused on the 'Agents id' section*/
#Agents{
    width: 85%;
    margin: 0 auto;
}

.agents-contents{
    display: flex;
    justify-content: center;
}

.agents{
    height: 100%;  
    padding: 2%; 
}

.agent-img{
    display: block;
    width: 100%;
    max-height: 300px;
    min-height: 100px;
    border-radius: 20px;
}

@media screen and (min-width:280px) and (max-width:700px){
    #home-img-container{
        height: 40%;
    }
}


@media screen and (max-width: 1100px) {
    .about-contents{
        flex-direction: column-reverse;
    } 
}

@media screen and (max-width: 600px) {
    .agents-contents{
        flex-direction: column-reverse;
    }
}

/*STYLING THE FOOTER */
footer {
    background: black;
    color: #fff;
    font-family: 'Fresca', sans-serif;
    font-family: 'Varela Round', sans-serif;
}

.contact-list {
    list-style-type: none;
}

.contact-list li a {
    color: #fff;
}

.footer-column1,
.footer-column2 {
    margin-top: 2%;
    transition: .4s;
}

.footer-column1:hover,
.footer-column2:hover {
    background-color: #d8d8d82e;
    border-radius: 5px;
}