#properties-main-content{
    background-image: url('./Home6.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-size: cover;
}

.view-properties-text{
    color: #fff;
    font-weight: bolder;
    text-align: center;
    padding-top: 6.5%;
}

.hr-properties{
    min-width: 300px;
    margin-bottom: 1.5%;
}

#properties-container{
    display: grid;
    grid-template-columns: auto auto auto;
    grid-gap: 15px;
    width: 85%;
    margin: 0 auto;
}

.house-description-container{
    border-radius: 35px;
    -webkit-box-shadow: 0px 5px 15px 5px #000000; 
    box-shadow: 0px 5px 15px 5px #363636;
    background-color: #fff;
    font-weight: bold;
    padding: 2%;
    max-height: 365px;
    max-width: 420px;
}

.img-container{
    height: 40%;
}

.house-img{
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 35px;
}

@media screen and (max-width: 600px) {

    #properties-container{
        display: grid;
        grid-template-columns: auto;
        grid-gap: 15px;
        width: 85%;
        margin: 0 auto;
    }

    .house-description-container{
        margin: 0 auto;
        border-radius: 35px;
        box-shadow: 0px 5px 15px 5px #363636;
        font-weight: bold;
        padding: 2%;
        max-width: 380px;
        min-width: 275px;
    }
}

