@import url('https://fonts.googleapis.com/css2?family=Fresca&family=Varela+Round&display=swap');
.big-screen{
    position: fixed;
    width: 100%;
    background: #2e2e2eba;
    display: flex;
    align-items: center;
    height: 9%;
    padding: 2%;
    color: #fff;
    font-family: 'Fresca', sans-serif;
    font-family: 'Varela Round', sans-serif;
}

.logo{
    width: 100%;
}

.nav-links{
    width: 100%;
}

.nav-links-list a{
    color: #fff;
    text-decoration: none;
}

.nav-links-list a:visited{
    text-decoration: none;
    color: #fff;
}

.navigation-off{
    width: 50%;
    background: #2e2e2ede;
    position: fixed;
    height: 100%;
    color: #ffffffa1;
    padding: 2%;
    transform: translateX(-100%);
}


.navigation-off{
    width: 50%;
    background: #2e2e2ede;
    position: fixed;
    height: 100%;
    color: #ffffffa1;
    padding: 2%;
    transform: translateX(-100%);
    transition: .4s;
}

.navigation-on{
    width: 50%;
    background: #2e2e2ede;
    position: fixed;
    height: 100%;
    color: #ffffffa1;
    padding: 2%;
    transform: translateX(0);
    transition: .4s; 
}

.nav-links-list{
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    list-style-type: none;
}



.nav-list{
    display: flex;
    flex-direction: column;  
    height: 30%; 
}

.nav-list li{
    display: inline-block;
    margin: 10% 10%;
}

@media screen and (min-width: 280px) and (max-width: 1028px){
    .image-container{
        min-width: 280px;
        max-width: 690px;
    }

    .form-container{
        min-width: 280px;
        max-width: 660px;
    }
    
}


@media screen and (max-width: 700px){

    .big-screen{
        height: 5%;
    }
    
    .sign-in-img{
        border-top-right-radius: 0px;
        border-bottom-left-radius: 30px;
    }

    .nav-list{
        height: 100%;
        list-style-type: none;
        display: flex;
        justify-content: space-evenly;
    }

    .nav-list li{
        display: inline-block;
        margin: 10% 10%;
        
    }
}


@media screen and (min-width: 701px) and (max-width: 900px) {
    .big-screen > .logo , .nav-links {
        display: none;
    }

    .nav-burger{
        display: inline;
    }
}


