*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body, #root, .App{
    height: 100%;
}

.main-content{
    display: flex;
    height: 100vh;
}

.big-screen{
    position: fixed;
    width: 100%;
    background: #2e2e2eba;
    display: flex;
    align-items: center;
    height: 9%;
    padding: 2%;
    color: #fff;
}

.logo{
    width: 100%;
}

.nav-links{
    width: 100%;
}

.nav-links-list a{
    color: #fff;
    text-decoration: none;
}

.nav-links-list a:visited{
    text-decoration: none;
    color: #fff;
}

.navigation-off{
    width: 50%;
    background: #2e2e2ede;
    position: fixed;
    height: 100%;
    color: #ffffffa1;
    padding: 2%;
    transform: translateX(-100%);
}


.navigation-off{
    width: 50%;
    background: #2e2e2ede;
    position: fixed;
    height: 100%;
    color: #ffffffa1;
    padding: 2%;
    transform: translateX(-100%);
    transition: .4s;
}

.navigation-on{
    width: 50%;
    background: #2e2e2ede;
    position: fixed;
    height: 100%;
    color: #ffffffa1;
    padding: 2%;
    transform: translateX(0);
    transition: .4s; 
}

.nav-links-list{
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    list-style-type: none;
}

.image-container{
    height: 100%;
    width: 100%;
}

.sign-in-img{
    display: block;
    width: 100%;
    height: 100%;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
}

.form{
    margin: 0 auto;
    width: 70%;
}

h2{
    margin: 2% 0;
    text-align: center;
}

hr{
    background-color: #ef7900;
    border: 2px solid #ef7900;
    width: 60%;
    margin: 1.5% auto;
}

.form-container{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
}

.sign-up-input{
    display: block;
    margin: 20% auto;
    padding: 2%;
    width: 100%;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 2px solid #ef7900;
    font-weight: 700;
}

.sign-up-input::placeholder{
    color: black;
}

.sign-up-input[type='submit']{
    background: #ef7900;
    border: none;
    border-radius: 30px;
}

.sign-up-input:focus{
    outline: none;
}


@media screen and (min-width: 280px) and (max-width: 1028px){
    .image-container{
        min-width: 280px;
        max-width: 690px;
    }

    .form-container{
        min-width: 280px;
        max-width: 660px;
    }
    
}
.nav-list{
    display: flex;
    flex-direction: column;  
    height: 30%; 
}

.nav-list li{
    display: inline-block;
    margin: 10% 10%;
}

@media screen and (max-width: 700px){
    .main-content{
        display: flex;
        flex-direction: column;
    }
}

@media screen and (max-width: 700px){zzz
    .big-screen > .logo , .nav-links {
        display: none;
    }
    
    .sign-in-img{
        border-top-right-radius: 0px;
        border-bottom-left-radius: 30px;
    }

    .nav-list{
        height: 100%;
        list-style-type: none;
        display: flex;
        justify-content: space-evenly;
    }

    .nav-list li{
        display: inline-block;
        margin: 10% 10%;
        
    }
}

@media screen and (min-width: 701px) {
    .nav-burger{
        display: none;
    }
}


